/*--------------------------Google Fonts----------------------*/
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600&family=Poppins:wght@500;600;700;800&family=Rubik:ital,wght@0,400;0,600;0,700;1,300;1,600;1,700&display=swap");

/*--------------------------CSS Variables----------------------*/
:root {
  /*---------Colors----------*/
  /*Color mode HSL(Hue,Saturation,Lightness)*/
  --first-color: hsl(236, 91%, 57%);
  --title-color: hsl(0, 0%, 40%);
  --text-color: hsl(0, 0%, 50%);
  --body-color: hsl(0, 0%, 100%);
  --container-color: hsl(0, 0%, 93%);
  --border-color: hsl(0, 0%, 87%);
  --text-white: #fff;
  /*---------Font and Typography----------*/
  --body-font: "Open Sans", sans-serif;
  --second-font: "Poppins", sans-serif;
  --big-font-size: 56px;
  --h1-font-size: 50px;
  --h2-font-size: 32px;
  --h3-font-size: 24px;
  --large-font-size: 18px;
  --normal-font-size: 16px;
  --small-font-size: 15px;
  --smaller-font-size: 14px;
  --tiny-font-size: 12px;
  /*---------Font weight----------*/
  --weight-400: 400;
  --weight-500: 500;
  --weight-600: 600;
  --weight-700: 700;
  --weight-800: 800;
}
.dark-theme{
  --title-color: hsl(0, 0%, 100%);
  --text-color: hsl(0, 0%, 80%);
  --body-color: hsl(0, 0%, 7%);
  --container-color: hsl(0, 1%, 17%);
  --border-color: hsl(0, 0%,15%);
}
/*--------------------------Base----------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--weight-500);
}
body {
  background-color: var(--body-color);
  color: var(--text-color);
}
h1,
h2,
h3,
h4 {
  color: var(--title-color);
  font-family: var(--second-font);
  font-weight: var(--weight-700);
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
button,
input,
textarea {
  background-color: transparent;
  border: none;
  outline: none;
}
img {
  max-width: 100%;
}
/*-------------------Reusable CSS---------------*/
.container{
  max-width:1140px;
  margin-inline:auto;
}
.grid{
  display:grid;
  gap:30px;
}
.section{
  padding-block:70px;
}
.section__title{
  font-size:var(--big-font-size);
  font-weight:var(--weight-600);
  text-align:center;
  margin-bottom:70px;
}
.section__title span{
  color:var(--first-color);
}
.button {
  display:inline-block;
  color:var(--title-color);
  border:1px solid var(--first-color);
  padding:15px 70px 15px 35px;
  border-radius:35px;
  font-family:var(--second-font);
  font-size:var(--small-font-size);
  font-weight:var(--weight-500);
  position:relative;
  overflow:hidden;
  transition:all 0.25s ease-in-out;
  margin-top:20px;
}
.button:hover{
  color:var(--text-white);
}
.button::before{
  content:"";
  position:absolute;
  inset:0;
  background-color:var(--first-color);
  z-index:-1;
  transform: translate(100%);
  transition:all 0.3s ease;
}
.button:hover::before{
  transform:translateX(0);
}
.button__icon{
  background-color:var(--first-color);
  position:absolute;
  inset-block:-2px;
  right:0;
  width:55px;
  border-radius:50%;
  color:var(--text-white);
  font-size:var(--large-font-size);
  text-align:center;
  line-height:60px;
}
