.about__container{
    display:flex;
    justify-content:space-between;
}
.info__list,.stats,.resume__container{
    grid-template-columns:repeat(2,1fr);
}
.info_list{
    row-gap:20px;
    margin-bottom:32px;
}
.section__subtitle{
    font-size:var(--h3-font-size);
    font-weight:var(--weight-600);
    margin-bottom:20px;
}
.info__list .info__title,
.info__list .info__description{
font-size:var(--small-font-size);
}
.info__list .info__description{
    color:var(--title-color);
    font-weight:var(--weight-600);
}
.stats__box{
    border:1px solid var(--border-color);
    padding:20px 30px 25px;
    border-radius:5px;
}
.stats__no{
    color:var(--first-color);
    font-size:var(--h1-font-size);
}
.stats__title{
    font-size:var(--small-font-size);
    line-height:1.6;
padding-left: 45px;
position:relative;
}
.stats__title::before{
    content:"";
    position:absolute;
    left:0;
    top:25%;
    width:30px;
    height:1px;
    background-color:var(--text-color);
}
.seperator{
    border-top:1px solid var(--border-color);
    max-width:40%;
    margin:72px auto 70px;
}
.skills__container{
    grid-template-columns: repeat(4,1fr);
    place-items: center;
    row-gap:48px;
}
.progress__circle{
    width:120px;
    height:120px;
    
}
.skills__title{
font-size:var(--normal-font-size);
font-family:var(--body-font);
font-weight:var(--weight-400);
text-align:center;
margin-top:24px;
}
.subtitle__center{
text-align:center;
margin-bottom:50px;
}
.CircularProgressbar-path{
    stroke:var(--first-color) ! important;
    stroke-linecap:butt !important;
}
.CircularProgressbar-trail{
    stroke:var(--contianer-color)!important;
}
.CircularProgressbar-text{
    fill:var(--title-color)!important;
    font-family:var(--second-font);
    font-size:var(--large-font-size);
}
.resume__item{
    position:relative;
    padding-left:60px;
}
.resume__item:not(:last-child){
    margin-bottom:50px;
}
.resume__item::before{
    content:"";
    position:absolute;
    inset-block:0;
    left:20px;
    border-left:1px solid var(--border-color);
}
.resume__icon{
    position:absolute;
    top:0;
    left:0;
    background-color:var(--first-color);
    color:var(--text-white);
    width:40px;
    height:40px;
    border-radius:50%;
    text-align:center;
    line-height:40px;
}
.resume__date{
    background-color:var(--container-color);
    padding:2px 10px;
    border-radius:20px;
    font-size:var(--timy-font-size);
    font-weight:var(--weight-600);
}
.resume__subtitle{
    font-size:var(--large-font-size);
    font-weight:var(--weight-500);
    margin-block:20px 10px;
}
.resume__subtitle span{
    color:var(--text-color);
    font-family:var(--body-font);
    font-size:var(--small-font-size);
    font-weight:var(--weight-600);
    position:relative;
    padding-left:26px;
}
.resume__subtitle span::before{
    content:"";
    position:absolute;
    left:7px;
    top:9px;
    width:10px;
    height:2px;
    background-color:var(--text-color);
}
.resume__description{
    font-size:var(--small-font-size);
    line-height:1.2;
}