.nav__menu {
  background-color: var(--container-color);
  position: fixed;
  inset-block: 0;
  width: 100%;
  padding: 60px 30px;
  left:-100%;
  transition:all 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.show-menu{
  left:0;  
}
.nav__link {
  display: flex;
  flex-direction:row;
  align-items: center;
  column-gap: 24px;
  border-bottom: 1px solid var(--border-color);
  padding-block: 14px;
}
.nav__icon,
.nav__name {
  color: var(--title-color);
  font-size: 24px;
}
.nav_name {
  font-weight: var(--weight-500);
}
.active__nav .nav__icon, 
.active__nav .nav__name{
color:var(--first-color);
}
.nav__toggle{
    position:fixed;
    top:30px;
    right:30px;
    width:40px;
    height:40px;
    background-color:var(--container-color);
    border-radius: 5px;
    padding:12px 10px;
}
.nav__toggle span{
    display:block;
    width:20px;
    height:2px;
    border-radius: 2px;
    background-color:var(--title-color);
    margin-bottom:5px;
}
.animate__toggle span:first-child{
    transform:rotate(45deg) translate(6px,4px);
}
.animate__toggle span:nth-child(2){
    display:none;
}
.animate__toggle span:last-child{
    transform:rotate(-45deg) translate(1px,1px);
}
@media screen and (min-width:1024px) {
    .nav__menu{
        all:unset;
    }
    .nav__toggle{
        display:none;
    }
    .nav{
        position:fixed;
        
        inset-block:0;
        right:30px;
        display:flex;
        align-items:center;
    }
    .nav__item{
        margin-block:20px;
    }
    .nav__link{
        padding:0;
        border-bottom:none;
        background-color:var(--container-color);
        width:50px;
        height:50px;
        border-radius:50%;
        position:relative;
    }
    .nav__icon{
        font-size:20px;
        margin-inline:auto;
    }
    .nav__name{
        position:absolute;
        top:0;
        right:0;
        background-color: var(--first-color);
        height:100%;
        line-height:50px;
        padding-inline:30px 25px;
        border-radius:30px;
        font-size:var(--small-font-size);
        color:var(--text-white);
        opacity:0;
        visibility:hidden;
        transition:all 0.3s ease;
    }
    .nav__link:hover .nav__name{
        right:31.5px;
        color:var(--text-white);
        opacity:1;
        visibility:visible;
        border-radius:30px 0 0 30px
    }
    .nav__link:hover,
    .active__nav{
background-color:var(--first-color);
    }
    .nav__link:hover .nav__icon,
    .active__nav .nav__icon{
color:var(--text-white);
    }
    
}