
.portfolio__container{
    grid-template-columns: repeat(3,1fr)!important;
    
}
.portfolio__item{
    position:relative;
    border-radius:10px;
    overflow:hidden;
    cursor:pointer;
}
.portfolio__img{
    height:300px;
    width:300px;
    object-fit:cover;
vertical-align:middle;
}
.portfolio__hover{
    background-color:var(--first-color);
    position:absolute;
    inset:0;
    display:grid;
    place-items:center;
    opacity:0;
    transition:all 0.5s ease;
}
.portfolio__item:hover .portfolio__hover{
    opacity:1;
}
.portfolio__title{
    color:var(--text-white);
    font-size:var(--large-font-size);
    font-weight:var(--weight-500);
    transform:translateY(-20px);
    transition:all 0.5s ease;
}
.portfolio__item:hover .portfolio__title{
    transform:translateY(0);
}
.portfolio__modal{
    position:fixed;
    inset:0;
    background-color:hsla(0,0%,0%,0.7);
    z-index:1000;
    display:grid;
    place-items:center;
    padding:15px;
}
.modal__close{
    width:50px;
    position: absolute;
    top:-3.5%;
    right:-8.5%;
    filter:invert(0.8);
}
.portfolio__modal-content{
    background-color:var(--container-color);
    max-width:650px;
    width:100%;
    padding:32px;
    border-radius:10px;
    position:relative;
}
.modal__img{
    width:200px;
    height:200px;
    object-fit:cover;
}
.modal__title{
    text-align:center;
    color:var(--first-color);
    font-size:var(--h2-font-size);
    letter-spacing:2px;
    margin-bottom:24px;
}
.modal__list{
    grid-template-columns: repeat(2,1fr);
    row-gap:10px;
    margin-bottom:24px;
}
.modal__item{
    display:flex;
    align-items:center;
    column-gap:8px;
}
.item__icon{
    display:flex;

}
.item__title,
.item__details{
font-size:var(--small-font-size);
color:var(--title-color);
}
.item__details{
    font-weight:var(--weight-600);
}